<template>
<div>
  <cool-select :arrowsDisableInstantSelection="true" placeholder="Busque pelo nome clique para adicionar à lista" v-model="entity" :items="list" item-text="title" @select="addItem()">
    <template slot="item" slot-scope="{ item: option }">
      <div style="display: flex; align-items: center;">
        <img v-if="option.picture" :src="baseUrl + option.picture">
        <div>
          <strong>{{ option.title }}</strong>
          <br>
          <small>{{ option.description }}</small>
        </div>
      </div>
    </template>
    <template slot="no-data">
      <br>
      <h5 class="text-center">Nenhum item encontrado</h5>
      <br>
    </template>
  </cool-select>
  <div class="entity-select-preview" v-if="preview && preview.length > 0">
    <h5> Selecionados: </h5>
    <div class="list-group">
      <div class="list-group-item" v-for="(item_preview, index) in preview" :key="index">
        <div>
          <img v-if="item_preview && item_preview.picture" :src="baseUrl + item_preview.picture" />
          <span v-if="item_preview">{{item_preview.title}}</span>
          <b-button v-if="item_preview" class="btn btn-xs btn-danger fa fa-trash pull-right" @click="removeItem(item_preview.id)"></b-button>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import {
  CoolSelect
} from 'vue-cool-select'
export default {
  name: 'form-entities-select',
  props: ['items', 'type', 'form', 'field'],
  inject: ['$validator'],
  data() {
    return {
      list: [],
      entity: null
    }
  },
  async created() {
    if (this.items) {
      this.list = this.items
    } else {
      switch (this.type) {
        case 'seeds':
          this.list = (await this.loadList('seeds')).map(seed => ({
            id: seed._id,
            title: seed.name,
            description: seed.scientific_name,
            picture: seed.images && seed.images.length ? seed.images[0].thumb : '',
          }))
          break;
        case 'collectors':
          this.list = (await this.loadList('collectors')).map(collector => ({
            id: collector._id,
            title: collector.name,
            description: collector.nickname,
            picture: collector.image ? collector.image.thumb : '',
          }))
          break;
        case 'collectors_groups':
          this.list = (await this.loadList('collectors_groups')).map(collectors_group => ({
            id: collectors_group._id,
            title: collectors_group.name,
            description: this.formatCity(collectors_group.address)
          }))
          break;
      }
    }
  },
  computed: {
    preview() {
      if (this.form && this.form[this.field] && this.list) {
        return this.form[this.field].map(selected => {
          return this.getItem(selected)
        }).filter(preview => preview)
      }
      return []
    }
  },
  methods: {
    addItem() {
      if (!this.form[this.field].find(item => (item == this.entity.id))) {
        this.form[this.field].push(this.entity.id)
      }
      this.entity = null
    },
    removeItem(id) {
      this.form[this.field] = this.form[this.field].filter(item => (item != id))
    },
    getItem(id) {
      return this.list.find(i => {
        return i.id == id
      })
    }
  },
  components: {
    CoolSelect
  }
};
</script>
